html {
  box-sizing: border-box;
  font-size: 62.5%;
  /*@media only screen and (max-width: $bp-large) {
    font-size: 50%;
  }*/
}

body {
  font-family: "Helvetica-Regular", sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.15;
  color: #ffffff;
  min-height: 100vh;
}

