a {
  color: #000000;
  text-decoration: none;
}

a:hover, a:focus {
  color: #333333;
}

.button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  appearance: none;
}

.container {
  width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 944px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1152px;
  }
}
.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  display: none;
}
.overlay.active {
  display: block;
}

.header__logo svg {
  width: 5.8rem;
  height: auto;
}
@media screen and (min-width: 768px) {
  .header__logo svg {
    width: 7.7rem;
  }
}

.header__menu-wrapper {
  display: none;
  position: fixed;
  bottom: 0;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
  transform: translateY(100%);
  z-index: 5;
}
.header__menu-wrapper.open {
  background-color: green;
  display: block;
  width: 100%;
  height: 80vh;
  transform: translateY(0);
}
.header__menu-icon img {
  width: 2.4rem;
  height: auto;
}
.header__menu-nav ul {
  padding-left: 0;
  list-style: none;
}
.header__menu-nav li:not(:last-child) {
  margin-bottom: 0.5rem;
}
@media screen and (min-width: 768px) {
  .header__menu-nav {
    display: block;
  }
  .header__menu-nav li {
    display: inline-block;
    position: relative;
  }
  .header__menu-nav a {
    padding: 0 0.5rem;
  }
  .header__menu-nav a.active::after {
    content: "";
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% + 1rem);
    height: 0.2rem;
    background-color: #000000;
  }
}

.header {
  background-color: #2659C3;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
}
.header__menu-toggle {
  display: block;
  position: absolute;
  left: 0;
  width: 2.4rem;
  height: auto;
}
.header__menu-toggle img {
  width: inherit;
  height: inherit;
}
@media screen and (min-width: 768px) {
  .header__menu-toggle {
    display: none;
  }
}